
.container {
  display:flex;
align-items: center;
  padding:0 50px;
  width:600px;
  height:250px;
  backdrop-filter: blur(2px);
  background:rgba(255,255,255,0.45);
  margin:auto;
  transition: height 0.6s ease , width 0.3s ease 0.6s;

}

.fullscreen {
  height: 95vh;
width: 80%;
}
